import React, { Component } from "react";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faTimesCircle,
  faCheckCircle,
  faTrashAlt
} from "@fortawesome/free-regular-svg-icons";

export default class About extends Component {
  render() {
    return (
      <div>
        <main className="container">
          <h2>About Todo App</h2>
          <h3 className="text-secondary">How to use Todo App:</h3>
          <ul className="bg-info container text-white">
            <li>Goto main page to see the List of all Todos</li>
            <li>click on the create todo in navigation bar to add a todo</li>
            <li>
              double click (only for PC) on the description or responsible or
              click on the edit icon to edi the todo in todo list
            </li>
            <li>Click on the check icon to mark todo as completed</li>
            <li>Click on the delete icon to delete the todo</li>
          </ul>
        </main>
        <footer>
          <p className="text-info">Version: 0.1</p>
        </footer>
      </div>
    );
  }
}
